import Vue from 'vue'
import VueRouter from 'vue-router'
import RecommendView from '../views/RecommendView.vue'

Vue.use(VueRouter)

const routes = [{
        path: "/", //默认首页
        component: RecommendView,
    },
    {
        path: "/HotMusic",
        // 异步懒加载
        component: () =>
            import ("../views/HotMusic.vue")
    },
    {
        path: "/Search",
        component: () =>
            import ("../views/SearchView.vue")
    },
    {
        path: "/SongsList/:listid",
        props: true,
        component: () =>
            import ("../views/SongsList.vue")
    },
    {
        path: "/SingerView/:id/",
        name: "SingerView",
        component: () =>
            import ("../views/SingerView.vue")
    }
]

const router = new VueRouter({
    mode: 'hash',
    base: process.env.BASE_URL,
    routes,
    scrollBehavior(to) {
        if (to.path == "/HotMusic") {
            return { x: 0, y: 0 }
        }
        return { x: 0, y: 0 }
    }
})

export default router