<template>
<!-- 全局播放组件 -->
  <div v-if="music" class="musicplay">
    <audio autoplay @play="playAudio" @pause="pauseAudio" @timeupdate="timeUpdate" ref="player" :src="`http://music.163.com/song/media/outer/url?id=${music.id}.mp3`"></audio>
    <div class="play-bar" @click.stop="isfull=true">
      <img :src="music.picUrl||music.al.picUrl" :style="{animationPlayState:playState?'running':'paused'}"/>
      <span>
        <div :style="{animationPlayState:playState?'running':'paused'}">
          {{music.name}}-<b v-for="(artist,index) in music.song.artists" :key="index+artist">{{artist.name}}</b>
        </div>
      </span>
      <i :class="['playbtn',{play:playState},{pause:!playState}]" @click.stop="changePlay"></i>
      <span class="gedan"></span>
    </div>
    <music-full-screen
     :music="music"
     @showfull="isfull=$event"
     :playState="playState"
     :currentTime="currentTime"
     :totalTime="totalTime"
     :playlist="playlist"
     @changetime="changeTime"
     @play="$emit('play',$event)"
    v-if="isfull">
    </music-full-screen>
  </div>
</template>

<script>
import MusicFullScreen from './MusicFullScreen.vue'
export default {
   props:{
    music:{
      type:Object,
      default:()=>{
        return null
      }
    },
    playlist:{
      type:Array,
      default:()=>{
        return null
      }
    }
   },
   data() {
       return {
            isfull:false,
            playState:true,
            currentTime:0,
            totalTime:0,
            
       }
   },
  // 组件注册
 components:{
     MusicFullScreen 
 },
 
   methods: {
       changePlay(){
        //  this.playState = !this.playState;
        let player=this.$refs.player;
        this.playState=!this.playState;
        if(this.playState){
            player.play();
        }else{
             player.pause();
        }
       },
       timeUpdate(event){
        let player=event.target;
        this.currentTime=player.currentTime;
        this.totalTime=player.duration;
        // console.log(currentTime,totalTime);
       },
       playAudio(){
         this.playState=true;
       },
       pauseAudio(){
         this.playState=false;
       },
       changeTime(time){
        this.$refs.player.currentTime=time;
       }   
   }
}
</script>

<style lang="scss" scoped>
.musicplay { 
 
  .play-bar {
    width: 100%;
    height: 60px;
    position: fixed;
    bottom: 0px;
    background-color: gray;
    display: flex;
    z-index: 9998;
    img {
      border-radius: 50%;
      width: 50px;
      height: 50px;
      margin-left: 20px;
      margin-top: 5px;
       animation: gundong 4s linear infinite;

    }

    span {
      div {
        // color: white;
        font-size: 16px;
        width: 150px;
        // border: 1px solid red;
        line-height: 60px;
        margin-left: 20px;
         overflow: hidden;
         white-space: nowrap;
        b {
        font-weight: normal;
        }
      }
    }
    i{
      width: 40px;
      height: 40px;
      // border-radius: 50%;
      margin-top: 10px;
      position: relative;
      margin-left: 10px;
      // border: 1px solid red;
       &.play::before{
         content: '';
          width: 30px;
        height: 30px;
        background-image: url(@/assets/zanting.svg);
        background-size: 30px 30px;
        background-repeat: no-repeat;
        background-position: 0 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
      }
      &.pause::after{
        content: '';
          width: 30px;
        height: 30px;
        background-image: url(@/assets/bofang.svg);
        background-size: 30px 30px;
        background-repeat: no-repeat;
        background-position: 0 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);

      }
    
      }
     
      .gedan{
            width: 40px;
            height: 40px;
            display: inline-block;
            background-image: url(@/assets/gedan.svg);
            background-size: 40px 40px;
            background-repeat: no-repeat;
            background-position: 0 0;
            margin-top: 10px;
            margin-left: 10px;
      }
    }
    
  }
   @keyframes gundong{
    from{
      transform: rotate(0deg);
    }to{
      transform: rotate(360deg);
    }
  }
 @keyframes movename{
   from{
     transform: translateX(100%);
   }
   to{
     transform: translateX(0%);
   }
 }
</style>
