<template>
  <div v-if="music" class="full-screen" @click.stop="showList=false">
    <div class="top-bar">
    <span class="title">{{music.name}}</span>
    <span class="back" @click.stop="$emit('showfull',false)">&lt;</span>
    <span class="fenxiang"></span>
    </div>
      <div class="mask">
          <img :src="music.picUrl||music.al.picUrl" :style="{animationPlayState: playState ? 'running' : 'paused'}">
      </div>
      <div class="lyric">
          <ul :style="{transform:`translateY(-${30*currentIndex}px)`}">
            <li :class="{active:index==currentIndex}" v-for="(item,index) in lyricArray" :key="index">{{item.lyric}}</li>
          </ul>
      </div>
        <div class="progress">
          <div class="cur">{{currentTime|formatMusicTime}}</div>
          <input type="range" min="0" max="100" v-model="rangetime">
          <div class="tot">{{totalTime|formatMusicTime}}</div>
          </div>
         <div class="bottom-bar">
           <div class="playmode">
          <span :class="[{order:playMode==3},{repeat:playMode==1},{shuffle:playMode==2}]" @click.stop="changeMode"></span>
          </div>
          <span class="prev-song" @click.stop="prevSong"></span>
          <i :class="[{play:playState},{pause:!playState}]" @click.stop="changePlay"></i>
          <span class="next-song" @click.stop="nextSong"></span>
          <span class="song-list" @click.stop="showList=true">
              <ul class="playlist" v-show="showList">
                <li class="bf-t">播放列表</li>
                <li :class="['name-list',{active:music.id==m.id}]" @click.stop="$emit('play',m)" v-for="(m,index) in playlist" :key="m.id+index">{{index+1}} . {{m.name}}</li>
              </ul>
          </span>
      </div>
  </div>
</template>

<script>
export default {
    props:{
        music:{
            type:Object,//类型为对象,默认值需要返回一个函数
            default:()=>{
                return null
            }
        },
        playState:{
          type:Boolean,
          default:false
        },
        currentTime:{
          type:Number,
          // default:0
        },
        totalTime:{
          type:Number,
          default:0
        },
         playlist:{
        type:Array,
        default:()=>{
          return null
         }
    },
    },
    data() {
      return {
        lyric:null,
        showList:false,
        playIndex:0,
        playMode:3,
        // rangetime:0
      }
    },
    created(){
    document.body.style.overflowY="hidden";
    this.getLyric();
    },
    beforeDestroy(){
    document.body.style.overflowY="auto";
    },
    methods: {
       changePlay(){
        this.$parent.changePlay();
       },
       changeShow(){
       this.showList=!this.showList;
       },
      async getLyric(){
        await this.axios.get("/lyric?id="+this.music.id).then(re=>{
            this.lyric=re.lrc.lyric;
            console.log('re==>',re);
            // console.log(this.lyric);
        })
      },
       findMusicIndex(){
     this.playlist.forEach((v,index) => {
       if(v.id==this.music.id){
          this.playIndex=index;
       }
     });
    },
    changeMode(){//切换播放模式
      this.playMode++;
      if(this.playMode>3){
        this.playMode=1;
      }
    },
    changePlayMode(){//当上一曲或下一曲的时候根据播放模式获取下一曲播放歌曲
      if(this.playMode==1){//单曲循环
          this.$emit("changetime",0);
      }else if(this.playMode==2){//随机播放
         let randomIndex=Math.floor(Math.random()*this.playlist.length);
          let currentMusic=this.playlist[randomIndex];
          this.$emit('play',currentMusic);
      }
    },
    prevSong(){
      if(this.playMode!=3) {
        return this.changePlayMode();
      }
      this.findMusicIndex();
      if(this.playIndex==0){
      this.playIndex=this.playlist.length-1;
      }else{
        this.playIndex--;
      }
      let currentMusic=this.playlist[this.playIndex];
      this.$emit('play',currentMusic);
    },
    nextSong(){
      if(this.playMode!=3) {
        return this.changePlayMode();
      }
      this.findMusicIndex();
      if(this.playIndex==this.playlist.length-1){
        this.playIndex=0;
      }else{
        this.playIndex++;
      }
      let currentMusic=this.playlist[this.playIndex];
      this.$emit('play',currentMusic);
    },
    },
    computed: {
      lyricArray(){
        // 异步时ajax没有获取到数据
        if(!this.lyric) return [];
        let lyricArr=this.lyric.split("\n");
        let lyricReg=/\[(\d+):(\d+\.\d+)\]([\s\S.]*)/;
        lyricArr.pop();
        return lyricArr.map(value=>{
          if(lyricReg.test(value)){
            return {
              time:parseInt(RegExp.$1*60)+parseFloat(RegExp.$2),
              lyric:RegExp.$3
            }
          }
        })
            //必须retrun结果
      },
      currentIndex(){
        let index=0;
        for(let i=0;i<this.lyricArray.length;i++){
        let ltime=this.lyricArray[i].time;
        let nextTime=this.lyricArray[i+1].time;
        if(this.currentTime>=ltime && this.currentTime<nextTime){
          index=i;
          return i;
        }
      }
      return index;
    },
    rangetime:{
      get(){
       return 100*(this.currentTime/this.totalTime);
      },
      set(value){
         let currentTime=this.totalTime*(value/100);
         this.$emit("changetime",currentTime);
      }
    }   
    },
     filters:{
      formatMusicTime(time){
       let minut=Math.floor(time/60);
       minut=minut<10?'0'+minut:minut;
       let second=Math.floor(time%60);
       second=second<10?'0'+second:second;
       return minut+':'+second;
      }
    },
    watch: {
      music:{
        deep:true,
        handler(){
         this.getLyric();
        }
      }
    },
  
}
</script>

<style lang="scss" scoped>
 .full-screen{
    position: fixed;
    z-index: 9999;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: gray;
  
   .top-bar{
     width: 100%;
     height: 30px;
     position: relative;
     text-align: center;
    .title{
        text-align: center;
        margin-top: 10px;
        color: white;
        display:inline-block;
        overflow: hidden;
      }
      .back{
        color: white;
        font-size: 30px;
        position: absolute;
        top: 0;
        left: 10px;
      }
     .fenxiang{
       width: 25px;
       height: 25px;
       display: inline-block;
       background-image: url(@/assets/fenxiang.svg);
        background-size: 25px 25px;
        background-repeat: no-repeat;
        background-position: 0 0;
        position: absolute;
        top: 5px;
        right: 10px;
     }
   }

  .mask {
      width: 40vw;
      height: 40vw;
      margin: 0 auto;
      border-radius: 50%;
      overflow: hidden;
      margin-top: 10px;
    img {
       display: block;
       width: 100%;
       animation: gundong 4s linear infinite;
    }
  }
  
   .lyric{
     width: 100%;
     text-align: center;
     height: 50vh;
     overflow: auto;
     margin-top: 15px;
     ul{
       li{
         color: white;
         padding-top: 20px;
         font-size: 14px;
         &.active{
           color: palegreen;
           font-weight: bold;
         }
       }
     }
   }
     .progress {
       height: 30px;
       width: 90vw;
       margin-top: 20px;
       display: flex;
       font-size: 12px;
       line-height: 28px;
       margin-left: 18px;
       color: white;
       .cur{
         padding-right: 5px;
       }
       .tot{
         padding-left: 5px;
       }
       input{
         width: 100%;
       }
    }
   
  .bottom-bar {
    width: 100%;
    height: 60px;
    // background-color: skyblue;
    position: fixed;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-around;
    padding-top: 20px;
    .playlist{
                height: 30vh;
                width: 100%;
                background-color: white;
                opacity: 0.7;
                border-radius:7% 7% 0 0;
                overflow-y: auto;
                position: fixed;
                bottom: 0px;
                left: 0;
                font-size: 15px;
                 .active{
                     color: gray;
                     font-weight: bold;
                   }
                .name-list{
                   padding-top: 10px;
                   padding-left: 5px;
                  
                }
                .bf-t{
                  text-align: center;
                  font-size: 20px;
                  font-weight: bold;
                }
             }
   i{
      width: 50px;
      height: 50px;
      margin-top: -8px;
      position: relative;
         &.play::before{
          content: '';
            width: 40px;
            height: 40px;
            background-image:url(@/assets/zanting.svg);
            background-size: 40px 40px;
            background-repeat: no-repeat;
            background-position: 0 0;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            }
          &.pause::after {
            content: '';
            width: 40px;
            height: 40px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%,-50%);
            background-image: url(@/assets/bofang.svg);
            background-size: 40px 40px;
            background-repeat: no-repeat;
            background-position: 0 0;
        }      
    }
     .prev-song{
       width: 30px;
       height: 30px;
       display: inline-block;
       background-image: url(@/assets/shangyiqu.svg);
        background-size: 30px 30px;
        background-repeat: no-repeat;
        background-position: 0 0;
       }
        .next-song {
       width: 30px;
       height: 30px;
       display: inline-block;
       background-image: url(@/assets/xiayiqu.svg);
        background-size: 30px 30px;
        background-repeat: no-repeat;
        background-position: 0 0;
        }

        .song-list {
           width: 30px;
            height: 30px;
            display: inline-block;
            background-image: url(@/assets/gedan.svg);
            background-size: 30px 30px;
            background-repeat: no-repeat;
            background-position: 0 0;     
           
        }
.playmode{
   width: 30px;
   height: 30px;
  .order {
           width: 30px;
            height: 30px;
            display: inline-block;
            background-image: url(@/assets/xunhuanbofang.svg);
            background-size: 30px 30px;
            background-repeat: no-repeat;
            background-position: 0 0;       
            
    }
     .repeat {
            width: 30px;
            height: 30px;
            display: inline-block;
            background-image: url(@/assets/danquxunhuan.svg);
            background-size: 30px 30px;
            background-repeat: no-repeat;
            background-position: 0 0;
    }
     .shuffle{
            width: 30px;
            height: 30px;
            display: inline-block;
            background-image: url(@/assets/suijibofang.svg);
            background-size: 30px 30px;
            background-repeat: no-repeat;
            background-position: 0 0;
   }
   }
    
    }  
  }
  @keyframes gundong{
    from{
      transform: rotate(0deg);
    }to{
      transform: rotate(360deg);
    }
  }


</style>