<template>
  <div class="recommend">
   <title-border>编辑推荐</title-border>
   <ul v-if="recommendList.length>0">
       <router-link tag="li" :to="`/SongsList/${item.id}`"  v-for="item in recommendList" :key="item.id">
           <images-item :item="item"></images-item>
           <p>{{item.name}}</p>
       </router-link>
   </ul>
   <load-animate v-else></load-animate>
   <title-border>最新音乐</title-border>
   <music-list v-if="newsongs.length>0" :musiclist="newsongs" @play="$emit('play',$event)"></music-list>
   <load-animate v-else></load-animate>
   <title-border>热门歌手</title-border>
   <div class="hotsinger">
   <div class="singer" v-for=" singer in hotsinger" :key="singer.id" @click.stop="GetSingerId(singer.id)">
   <img :src="singer.picUrl" alt="">
    {{singer.name}}
    <!-- <singer-view v-if="isfull" :singerId="singer.id"></singer-view> -->
   </div>
   </div>
   <title-border>推荐MV</title-border>
   <div class="recommendedMv" v-for="mv in recommendedMv" :key="mv.id">
    <mv-play :mvid="mv.id"></mv-play>
     <div class="mvname">
       {{mv.name}}
       </div>
       <div class="arname">
       {{mv.artistName}} 
       </div>
   </div>
   <div class="loadmore" ref='loadmore'>没有更多啦(●'◡'●)</div>
   <router-view></router-view>
  </div>
</template>

<script>
import TitleBorder from '@/components/TitleBorder.vue'
import ImagesItem from '@/components/ImagesItem.vue'
import MusicList from '@/components/MusicList.vue'
import LoadAnimate from '@/components/LoadAnimate.vue'
import MvPlay from '@/components//MvPlay.vue'


// import SongsList from '@/components/SongsList.vue'

export default {
data() {
    return {
      recommendList:[],
      newsongs:[],
      hotsinger:[],
      recommendedMv:[],
      mvplay:[],
      isfull:false,
    }
},
created(){
    this.getRecommendList();
    this.getNeweSongs();
    this.getHotSinger();
    this.GetNewMV();
},
methods: {
    async getRecommendList(){
        await this.axios.get('/personalized').then(re=>{
        this.recommendList=re.result.slice(0,6)
        // console.log(this.recommendList);
        })
    },
    async getNeweSongs(){
    await this.axios.get('/personalized/newsong').then(re=>{
      this.newsongs=re.result
      // console.log('newsongs==>>',this.newsongs); 
    })
  },
   async getHotSinger(){
     await this.axios.get('/top/artists?offset=0&limit=30').then(re=>{
        this.hotsinger=re.artists
        console.log(this.hotsinger);
     })
   },
    async GetNewMV(){
     await this.axios.get('/personalized/mv').then(re=>{
        this.recommendedMv=re.result
        console.log('this.recommendedMv',this.recommendedMv);
     })
   },
   GetSingerId(id){
    this.$router.push({
      name:"SingerView",
      params:{
        id
      }
    })
   }
},
components:{
    TitleBorder,
    ImagesItem,
     MusicList,
     LoadAnimate,
     MvPlay,
    //  SongsList
}
}
</script>

<style lang="scss" scoped>
.recommend{
    ul{
        display: flex;
        flex-wrap: wrap; 
        justify-content: space-between; 
         
        li{
          width: 33%;
          p{
                  display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
                overflow: hidden;
                 text-overflow: ellipsis;
                word-break: normal;
                padding: 2px 2px 0 6px;
                min-height: 30px;
                line-height: 1.2;
                font-size: 13px;
          }
        }
    }
    .hotsinger{
      display: flex;
      width: 100%; 
      flex-wrap: wrap;
      justify-content: space-around;
      .singer{
        width: 100px;
        height: 100px;
        text-align: center;
        font-size: 14px;
        padding-top: 10px;
        color: grey;
        img{
          width: 100%;
          height: 85%;
          border-radius: 7%;
        }
      }
    }
    .recommendedMv {
      width: 100%;
      padding: 5px 0px;
      
  .mvname {
    font-size: 16px;
    font-weight: bold;
    padding: 8px 8px;
    line-height: 16px;
    color: black;
  }
  .arname{
    font-size: 13px;
    color: grey;
    padding:0px 14px;
  }
}
     .loadmore{
   text-align: center;
   font-size: 14px;
   line-height: 40px;
 }
}
</style>