<template>
  <div id="app">
    <tab-nav></tab-nav>
    <router-view @play="play"></router-view>
    <music-play :music="currentMusic" :playlist="playList" @play="play"></music-play>
     
  </div>
</template>
<script>
import TabNav from "@/components/TabNav"
import MusicPlay from "@/components/MusicPlay"
import _ from 'lodash'

export default {
  data() {
    return {
      currentMusic:null,
      playList:[]
    }
  },
  components:{
    TabNav,MusicPlay
  },methods: {
    play(music){
      // console.log(music);
    this.currentMusic=music;
    this.playList.push(music);
    this.playList=_.uniq(this.playList)
    }
  },
}
</script>
<style lang="scss">
#app{
  min-height: 100vh;
  background-color: white;
}

body{
  margin: 0;
  box-sizing: border-box;
}


</style>
