<template>
  <div class="images-item">
   <img :src="item.picUrl">
<span>{{item.playCount|formatNum}}</span>
  </div>
</template>

<script>
export default {
props:{
    item:{
      type:[Object,String],
      default:''
    }
},
filters: {
  formatNum(value){
    let val=Math.floor(value/1000);
    return (val/10)+"万";
  }
}
}
</script>

<style lang="scss" scoped>
.images-item{
    position: relative;
    img{
        display: block;
        width: 100%;
        z-index: 1;
    }
    span{
      color: white;
      position: absolute;
      right: 5px;
      top: 2px;
      z-index: 3;
      padding-left: 13px;
      color: #fff;
      font-size: 12px;
      background-position: 0;
      background-repeat: no-repeat;
      background-size: 11px 10px;
      text-shadow: 1px 0 0 rgb(0 0 0 / 15%);
      background-image: url(data:image/svg+xml;base64,);

    }
     &::after{
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 20px;
        z-index: 9;
        background-image: -webkit-linear-gradient(top,rgba(0,0,0,.2),rgba(0,0,0,0));
        background-image: linear-gradient(180deg,rgba(0,0,0,.2),rgba(0,0,0,0));
      }
}
</style>