<!--  -->
<template>
<div class='mvplay'>
    <div class="play">
    <video width="100%" height="100%" ref="" controls="controls" type="video/mp4" loop="loop" muted="muted" :src="mvurl">
        <!-- autoplay自动播放 controls播放控件 loop循环播放 poster加载等待画面 muted 静音播放(解决谷歌禁用自动播放) -->     
    </video>
    </div>
</div>
</template>

<script>

export default {
props:{
    mvid:{
        default:"0",
    }
},
//import引入的组件需要注入到对象中才能使用
components: {},

data() {
//这里存放数据
return {
    mvurl:{}
};
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
  this.GetMvPlay();
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {
},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成 
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发
//方法集合
methods: {
 async GetMvPlay(){
     console.log(this.mvid);
     await this.axios.get(`/mv/url?id=${this.mvid}`).then(re=>{
        this.mvurl=re.url
        console.log('this.mvurl',this.mvurl);
     })
   },
},
}
</script>
<style lang='scss' scoped>
  .play{
      height: 250px;
  }
</style>